import { create } from 'zustand';
import { subscribeWithSelector } from 'zustand/middleware';
import { persist } from 'zustand/middleware';
import {
  NetworkDevice,
  DeviceFolder,
  NetworkMetrics,
  BandwidthData,
  AlertMessage,
  SystemHealth,
  NetworkConnection,
  MonitoringConfig,
} from '../types/schemas';
import {
  mockDevices,
  mockFolders,
  mockAlerts,
  initialMetrics,
  initialSystemHealth,
  initialBandwidthHistory,
  initialConfig,
} from '../data/mock-data';

interface NetworkStore {
  // State
  devices: NetworkDevice[];
  folders: DeviceFolder[];
  metrics: NetworkMetrics;
  bandwidthHistory: BandwidthData[];
  alerts: AlertMessage[];
  systemHealth: SystemHealth;
  connections: NetworkConnection[];
  config: MonitoringConfig;

  // UI State
  selectedFolderId: string;
  sidebarCollapsed: boolean;
  loading: boolean;
  error: string | null;
  rootFolderExpanded: boolean;
  othersExpanded: boolean;

  // Device actions
  addDevice: (
    device: Omit<NetworkDevice, 'id' | 'lastSeen' | 'status'>
  ) => void;
  updateDevice: (deviceId: string, updates: Partial<NetworkDevice>) => void;
  deleteDevice: (deviceId: string) => void;

  // Folder actions
  addFolder: (folder: Omit<DeviceFolder, 'id' | 'children'>) => void;
  updateFolder: (folderId: string, updates: Partial<DeviceFolder>) => void;
  deleteFolder: (folderId: string) => void;

  // Alert actions
  addAlert: (alert: Omit<AlertMessage, 'id' | 'timestamp'>) => void;
  markAlertResolved: (alertId: string) => void;
  acknowledgeAlert: (alertId: string, userId: string) => void;
  clearAlerts: () => void;

  // Data actions
  setBandwidthData: (data: BandwidthData[]) => void;
  addBandwidthData: (data: BandwidthData) => void;
  updateSystemHealth: (health: SystemHealth) => void;
  updateMetrics: (metrics: Partial<NetworkMetrics>) => void;

  // UI actions
  setSelectedFolder: (folderId: string) => void;
  setSidebarCollapsed: (collapsed: boolean) => void;
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;

  // Data management
  refreshData: () => Promise<void>;
  exportData: () => string;
  importData: (jsonData: string) => boolean;
  resetData: () => void;
  resetToMockData: () => void;
}

export const useNetworkStore = create<NetworkStore>()(
  subscribeWithSelector(
    persist(
      (set, get) => ({
        // Initial state with mock data
        devices: mockDevices,
        folders: mockFolders,
        metrics: initialMetrics,
        bandwidthHistory: initialBandwidthHistory,
        alerts: mockAlerts,
        systemHealth: initialSystemHealth,
        connections: [],
        config: initialConfig,

        // UI state
        selectedFolderId: 'root',
        sidebarCollapsed: false,
        loading: false,
        error: null,
        rootFolderExpanded: true,
        othersExpanded: false,

        // Device actions
        addDevice: (deviceData) => {
          const device: NetworkDevice = {
            ...deviceData,
            id: Math.random().toString(36).substr(2, 9),
            lastSeen: new Date(),
            status: 'online',
          };

          set((state) => ({
            devices: [...state.devices, device],
          }));

          // Update metrics
          get().updateMetrics({});
        },

        updateDevice: (deviceId, updates) => {
          set((state) => ({
            devices: state.devices.map((device) =>
              device.id === deviceId
                ? { ...device, ...updates, lastSeen: new Date() }
                : device
            ),
          }));

          // Update metrics
          get().updateMetrics({});
        },

        deleteDevice: (deviceId) => {
          set((state) => ({
            devices: state.devices.filter((device) => device.id !== deviceId),
          }));

          // Update metrics
          get().updateMetrics({});
        },

        // Folder actions
        addFolder: (folderData) => {
          const folder: DeviceFolder = {
            ...folderData,
            id: Math.random().toString(36).substr(2, 9),
            children: [],
          };

          const addFolderRecursive = (
            folders: DeviceFolder[]
          ): DeviceFolder[] => {
            return folders.map((f) =>
              f.id === (folder.parentId || 'root')
                ? { ...f, children: [...f.children, folder] }
                : { ...f, children: addFolderRecursive(f.children) }
            );
          };

          if (!folder.parentId || folder.parentId === 'root') {
            set((state) => ({
              folders: [...state.folders, folder],
            }));
          } else {
            set((state) => ({
              folders: addFolderRecursive(state.folders),
            }));
          }
        },

        updateFolder: (folderId, updates) => {
          // Специальная обработка для корневой папки
          if (folderId === 'root') {
            set((state) => ({
              rootFolderExpanded:
                updates.expanded !== undefined
                  ? updates.expanded
                  : state.rootFolderExpanded,
            }));
            return;
          }

          // Специальная обработка для папки "Иные"
          if (folderId === 'others') {
            set((state) => ({
              othersExpanded:
                updates.expanded !== undefined
                  ? updates.expanded
                  : state.othersExpanded,
            }));
            return;
          }

          const updateFolderRecursive = (
            folders: DeviceFolder[]
          ): DeviceFolder[] => {
            return folders.map((folder) =>
              folder.id === folderId
                ? { ...folder, ...updates }
                : {
                    ...folder,
                    children: updateFolderRecursive(folder.children),
                  }
            );
          };

          set((state) => ({
            folders: updateFolderRecursive(state.folders),
          }));
        },

        deleteFolder: (folderId) => {
          const removeFolderRecursive = (
            folders: DeviceFolder[]
          ): DeviceFolder[] => {
            return folders
              .filter((folder) => folder.id !== folderId)
              .map((folder) => ({
                ...folder,
                children: removeFolderRecursive(folder.children),
              }));
          };

          set((state) => ({
            folders: removeFolderRecursive(state.folders),
            devices: state.devices.map((device) =>
              device.folderId === folderId
                ? { ...device, folderId: 'root' }
                : device
            ),
          }));
        },

        // Alert actions
        addAlert: (alertData) => {
          const alert: AlertMessage = {
            ...alertData,
            id: Math.random().toString(36).substr(2, 9),
            timestamp: new Date(),
          };

          set((state) => ({
            alerts: [alert, ...state.alerts].slice(0, 50), // Keep only last 50 alerts
          }));
        },

        markAlertResolved: (alertId) => {
          set((state) => ({
            alerts: state.alerts.map((alert) =>
              alert.id === alertId ? { ...alert, resolved: true } : alert
            ),
          }));
        },

        acknowledgeAlert: (alertId, userId) => {
          set((state) => ({
            alerts: state.alerts.map((alert) =>
              alert.id === alertId
                ? {
                    ...alert,
                    acknowledgedBy: userId,
                    acknowledgedAt: new Date(),
                  }
                : alert
            ),
          }));
        },

        clearAlerts: () => {
          set({ alerts: [] });
        },

        // Data actions
        setBandwidthData: (data) => {
          set({ bandwidthHistory: data });
        },

        addBandwidthData: (data) => {
          set((state) => ({
            bandwidthHistory: [...state.bandwidthHistory.slice(-19), data],
          }));
        },

        updateSystemHealth: (health) => {
          set({ systemHealth: health });
        },

        updateMetrics: (metricsUpdate) => {
          const state = get();
          const devices = state.devices;

          const totalDevices = devices.length;
          const onlineDevices = devices.filter(
            (d) => d.status === 'online'
          ).length;
          const offlineDevices = devices.filter(
            (d) => d.status === 'offline'
          ).length;
          const warningDevices = devices.filter(
            (d) => d.status === 'warning'
          ).length;
          const averageResponseTime =
            totalDevices > 0
              ? Math.round(
                  devices.reduce((sum, d) => sum + d.responseTime, 0) /
                    totalDevices
                )
              : 0;

          set({
            metrics: {
              ...state.metrics,
              totalDevices,
              onlineDevices,
              offlineDevices,
              warningDevices,
              averageResponseTime,
              lastUpdate: new Date(),
              ...metricsUpdate,
            },
          });
        },

        // UI actions
        setSelectedFolder: (folderId) => {
          set({ selectedFolderId: folderId });
        },

        setSidebarCollapsed: (collapsed) => {
          set({ sidebarCollapsed: collapsed });
        },

        setLoading: (loading) => {
          set({ loading });
        },

        setError: (error) => {
          set({ error });
        },

        // Data management
        refreshData: async () => {
          set({ loading: true, error: null });
          try {
            // Simulate API call
            await new Promise((resolve) => setTimeout(resolve, 1000));
            get().updateMetrics({});
            set({ loading: false });
          } catch (error) {
            set({
              loading: false,
              error:
                error instanceof Error
                  ? error.message
                  : 'Ошибка загрузки данных',
            });
          }
        },

        exportData: () => {
          const state = get();
          const exportData = {
            devices: state.devices,
            folders: state.folders,
            connections: state.connections,
            config: state.config,
            exportTimestamp: new Date().toISOString(),
          };
          return JSON.stringify(exportData, null, 2);
        },

        importData: (jsonData) => {
          try {
            const data = JSON.parse(jsonData);
            set({
              devices: data.devices || [],
              folders: data.folders || [],
              connections: data.connections || [],
              config: data.config || initialConfig,
            });
            get().updateMetrics({});
            return true;
          } catch {
            return false;
          }
        },

        resetData: () => {
          set({
            devices: [],
            folders: [],
            metrics: initialMetrics,
            bandwidthHistory: [],
            alerts: [],
            systemHealth: initialSystemHealth,
            connections: [],
            selectedFolderId: 'root',
            loading: false,
            error: null,
          });
        },

        resetToMockData: () => {
          set({
            devices: mockDevices,
            folders: mockFolders,
            metrics: initialMetrics,
            bandwidthHistory: initialBandwidthHistory,
            alerts: mockAlerts,
            systemHealth: initialSystemHealth,
            connections: [],
            selectedFolderId: 'root',
            loading: false,
            error: null,
            rootFolderExpanded: true,
            othersExpanded: false,
          });
          get().updateMetrics({});
        },
      }),
      {
        name: 'network-monitor-storage',
        partialize: (state) => ({
          devices: state.devices,
          folders: state.folders,
          config: state.config,
          selectedFolderId: state.selectedFolderId,
          sidebarCollapsed: state.sidebarCollapsed,
        }),
      }
    )
  )
);
